/* ==========================================================================
   ICONS
   ========================================================================== */

svg {
    display: inline;
    max-width: 100%;
    max-height: 100%;
    vertical-align: baseline;
}

.heroicon-component-accent {
    fill: theme('colors.blue');
}

[class^="icon"] {
	display: inline-block;
	font-size: 16px;

	.icon-group & {
		margin: 0 0 0 10px;
		vertical-align: middle;
	}
}
.icon-left { margin-right: 10px; }
.icon-right { margin-left: 10px; }

.icon-status, .little-dot {
	border-radius: 7px;
	display: inline-block;
	height: 7px;
	position: relative;
	width: 7px;
    flex-shrink: 0;

    &.published { @apply bg-green-light }
    &.draft, &.expired { @apply bg-grey-60 }
    &.scheduled { @apply bg-transparent border border-grey-60 }
}

.icon-status-draft {
	background: $color_gray_light;
}
.icon-status-hidden {
	border: solid 1px $color_gray_light;
}
.icon-status-live {
	background: $color_green;
}
.icon-status-error {
	background: $color_red;
}
.icon-status-warning {
    @apply .bg-yellow-dark;
}

.icon-large {
    font-size: 42px;
}

.icon-header-avatar {
    height: 32px;
    width: 32px;
    line-height: 32px;
    @apply rounded-full shadow overflow-hidden;
}

.icon-user-initials {
    @apply text-center text-white text-2xs bg-pink;
}

.svg-icon {
    @apply w-4 h-4;

	&.using-svg {
		@apply .flex .items-center;
	}

	svg {
		width: 100%;
		height: 100%;
	}

	div {
		padding-top: 100%;
		background-repeat: no-repeat;
		background-size: contain;
		background-position: 50% 50%;
	}
}


/* Entypo
  ========================================================================== */

.icon:before {
    position: relative;
    top: 2px;
    display: inline-block;
    font-family: "entypo";
    speak: none;
    font-size: 100%;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

